// mock functions
function getAll (collection) {
  return cloneDeep(mock[collection])
}

function get (collection, query) {
  const data = cloneDeep(mock[collection]
    .filter((item) => {
      let match = true
      for (const field in query) {
        if (item[field] !== query[field]) {
          match = false
        }
      }
      return match
    }))
  return data
}

function getById (collection, id) {
  const data = cloneDeep(mock[collection].find((item) => item.id === id))
  return data
}

function add (collection, item) {
  item.id = (Math.max.apply(Math, mock[collection].map((item) => parseInt(item.id))) + 1).toString()
  mock[collection].push(item)
  return cloneDeep(item)
}

function update (collection, updatedItem) {
  mock[collection].forEach((item) => {
    if (item.id === updatedItem.id) {
      item = Object.assign(item, updatedItem)
    }
  })
  return cloneDeep(updatedItem)
}

function remove (collection, id) {
  const index = mock[collection].findIndex((item) => item.id === id)
  mock[collection].splice(index, 1)
}

function getEvents () {
  const value = Math.floor(1250 * (0.9 + Math.random() / 4))
  return [
    { type: 'stateUpdate', payload: { id: '4', currentValue: value, date: vue.$moment().format(), eqId: '2' } },
    { type: 'equipmentUpdate', payload: { id: '2', roomId: '1', lastCommunication: vue.$moment().format() } },
    { type: 'roomSummaryUpdate', payload: { id: '1', key: 'power', value } },
    { type: 'roomSummaryUpdate', payload: { id: 'global', key: 'power', value } },
  ]
}

