    // try to ask a question
    async askQuestion (query, replyCmd = null) {
      const result = get('queries', { query })
      if (result.length > 0) {
        return result[0].reply
      } else {
        const replies = ['Désolé, je n\'ai pas compris la demande', 'Je ne comprends pas', 'Je ne sais pas traiter cette requête :(']
        return replies[Math.floor(Math.random() * replies.length)]
      }
    },

